@import url(https://fonts.googleapis.com/css2?family=Lobster+Two&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

body {
  background-color: #333 !important;
}
.app-title {
  font-family: 'Lobster Two', cursive;
  color: #fff;
  font-size: 50px;
  text-align: center;
  margin: 0;
  padding: 20px;
}

.kanban-board {
  display: flex;
}

.column {
  display: flex;
  flex-direction: column;
  flex: 1 1;
  border: 1.5px solid #ffffff;
  margin: 10px;
  background-color: #ebecf0;
  border-radius: 10px;
  overflow: hidden;
  padding: 0px 10px 30px 10px;
  grid-gap: 20px;
  gap: 20px;
}

.column-heading {
  margin: 0;
  border-bottom: 1.5px solid #fff;
  font-family: 'Lobster Two', cursive;
  color: #1c1c1c;
  font-style: normal;
  font-weight: 700;
  font-size: 24px;
  line-height: 29px;
  padding: 10px 0;
}

.task-list {
  min-height: 100px;
  display: flex;
  flex-direction: column;
  grid-gap: 20px;
  gap: 20px;
}

.task {
  padding: 10px;
  border: 1px solid #ccc;
  position: relative;
  background: #ffffff;
  box-shadow: 0px 3px 0px #b2b8c4;
  border-radius: 9px;
}

.task:hover .btn-delete-task {
  display: block;
}

form {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  margin: 10px;
  flex-direction: row;
}

form label {
  margin-right: 10px;
  flex: 1 1 100%;
  font-family: 'Lobster Two', cursive;
  color: #fff;
}

form input[type='text'],
form select {
  padding: 4px;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin-bottom: 10px;
  margin-left: 8px;
  flex: 1 1 100%;
}

form button[type='submit'] {
  padding: 4px 16px;
  background: #4caf50;
  color: #fff;
  border-radius: 5px;
  border: none;
  cursor: pointer;
  margin-left: 10px;
  font-family: 'Lobster Two', cursive;
}

.btn-delete-task {
  display: none;
  position: absolute;
  top: -10px;
  right: -10px;
  border-radius: 10em;
  padding: 2px 6px 3px;
  text-decoration: none;
  font: 700 21px/20px sans-serif;
  background: #555;
  border: 3px solid #fff;
  color: #fff;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.5), inset 0 2px 4px rgba(0, 0, 0, 0.3);
  text-shadow: 0 1px 2px rgba(0, 0, 0, 0.5);
  transition: background 0.5s;
}

.btn-delete-task:hover {
  background: #e54e4e;
  padding: 3px 7px 5px;
  top: -11px;
  right: -11px;
}
.btn-delete-task:active {
  background: #e54e4e;
  top: -10px;
  right: -11px;
}

